export default {
    data() {
        return {
            items: []
        }
    },
    methods: {
        parseIntro(content) {
            if (content) {
                return content.replace("\n", "<br/>");
            }
        }
    },
    mounted() {
        if (USER.token) {
            this.$http({
                url: `${BASE_URL}/lock-account/advertise_event`,
                params: {
                    page_num: 1,
                    row_per_page: 10000
                }
            }).then((result) => {
                const data = result.data;
                if (data.status === 0) {
                    this.items = data.data;
                }
            });
        }
    }
}



